import axios from "axios";
import { Buffer } from "buffer";

const API_URL = "https://sccotview.com/api/";

/**
 * Lista de endpoints que no requieren autenticación
 * Puedes ajustar esto según los endpoints de tu backend que son públicos.
 */
const PUBLIC_ENDPOINTS = [
  "free/",
  "free",         // Para GET general de videos
           // Para GET general de series
];

/**
 * Instancia de Axios para hacer peticiones al backend
 */
const token = localStorage.getItem("access_token");

const axiosInstance = axios.create({
  baseURL: API_URL,
  timeout: 5000,
  headers: {
    "Content-Type": "multipart/form-data",  // Cambiado según tu observación
    accept: "application/json",
  },
});


/**
 * Interceptor de request para agregar el token solo si la ruta NO es pública
 */
axiosInstance.interceptors.request.use((config) => {
  const token = localStorage.getItem("access_token");

  // Detectar si la URL es pública
  const isPublic = PUBLIC_ENDPOINTS.some(endpoint => config.url.includes(endpoint));

  if (token && !isPublic) {
    config.headers["Authorization"] = "JWT " + token;
  } else {
    delete config.headers["Authorization"]; // Asegura que no se mande nada si no es necesario
  }

  return config;
});

/**
 * Interceptor de respuesta para manejar expiración del token
 */
axiosInstance.interceptors.response.use(
  (response) => {
    return response;
  },
  async function (error) {
    const originalRequest = error.config;

    if (typeof error.response === "undefined") {
      console.warn(
        "A server/network error occurred. " +
        "Looks like CORS might be the problem."
      );
      return Promise.reject(error);
    }

    if (
      error.response.status === 401 &&
      originalRequest.url === API_URL + "api/token/refresh/"
    ) {
      window.location.href = "/userlogin1/";
      return Promise.reject(error);
    }

    if (
      error.response.data.code === "token_not_valid" &&
      error.response.status === 401 &&
      error.response.statusText === "Unauthorized"
    ) {
      const refreshToken = localStorage.getItem("refresh_token");

      if (refreshToken) {
        const tokenParts = JSON.parse(
          Buffer.from(refreshToken.split(".")[1], "base64").toString()
        );

        const now = Math.ceil(Date.now() / 1000);

        if (tokenParts.exp > now) {
          try {
            const response = await axiosInstance.post("api/token/refresh/", {
              refresh: refreshToken,
            });

            localStorage.setItem("access_token", response.data.access_token);
            localStorage.setItem("refresh_token", response.data.refresh_token);

            axiosInstance.defaults.headers["Authorization"] =
              "JWT " + response.data.access_token;
            originalRequest.headers["Authorization"] =
              "JWT " + response.data.access_token;

            return axiosInstance(originalRequest);
          } catch (err) {
            console.log(err);
          }
        } else {
          console.log("Refresh token is expired", tokenParts.exp, now);
          localStorage.clear();
          window.location.href = "/userlogin2/";
        }
      } else {
        console.log("Refresh token not available.");
        localStorage.clear();
        window.location.href = "/userlogin3/";
      }
    }

    if (error.response.status === 401) {
      localStorage.clear();
    }

    return Promise.reject(error);
  }
);

export default axiosInstance;